import React, { useEffect, useState } from 'react';
import './App.css';
import Button from '@mui/material/Button';
const { ipcRenderer } = window.require('electron');

function App() {
  const [str, setStr] = useState('eeeee');

  ipcRenderer.on('url-reply', (event: any, data: any) => {
    setStr(data);
  });

  return (
    <div className='App'>
      <Button variant='contained' onClick={() => ipcRenderer.send('test', 'tttttt')}>
        ssssenew
      </Button>
      {str}
    </div>
  );
}

export default App;
